@media(max-width: 1440px){
    .logo{
        img{
            width: 100px;
        }
    }
    .header_title{
        font-size: 130px;
        line-height: 108px;
        top: 20px;
        left: 60px;
    }
    .menu{
        padding-top:10px;
    }
    .header_search{
        padding-bottom: 8px;
    }
    .header_date{
        font-size: 60px;
        span{
            font-size: 14px;
            padding-top: 0;
            margin-bottom: 5px;
        }
    }
    .header_arrows{
        left: 130px;
    }
    .surf-box {
        min-height: 650px;
    }
   
}

@media(max-width: 1366px){
 .header_aside{
     position: absolute;
     border-right: none;
     height: 108px;
     padding-right: 25px;
     background-color: rgba(#fff, .1);
     text-align: left;
     display: flex;
 }
 .menu{
     padding-top: 0;
 }
 .menu_list{
     text-align: center;
     li{
         display: inline-block;
     }
     a{
         padding: 15px;
     }
    }
    .header_search{
        padding: 33px 15px 15px;
    }
    .header_date{
        padding: 15px;
    }
    .header_location{
        margin-top: 42px;
    }
    .slider-item_info-title{
        font-size: 65px;
    }
    .header_arrows{
        left: 20px;
        bottom: 20px;
    }
    .shop__slider-content {
        padding-left: 40px;
    }
    .shop__slider-price {
        margin-bottom: 30px;
    }
}

@media(max-width: 1340px){
    .header_map{
        svg{
            width: 500px;
            height: 250px;
        }
    }
    .slider-arrows{
        top: 20px;
        right: 20px;
    }
    .slider-arrows_left{
        right: 60px;
    }
}

@media(max-width: 1287px){
    .slider-map .slick-list{
        overflow: hidden;
    }
    .slider-dots__circle{
        box-shadow: none;
    }
}
@media(max-width: 1250px){
    .slider-map{
        max-width: 1000px;
        min-height: 515px;
    }
    .slider-map .slick-list {
        min-height: 515px;
    }

    .slider-dots--1{
        top: 266px;
        .slider-dots__content{
            transform: translateX(0);
            &::before{
              left: -0.5px;
            }
        }
      }
      .slider-dots--2{
        top: 232px;
      } 
      .slider-dots--3{
        top: 253px;
      } 
      .slider-dots--4{
        top: 390px;
      } 
      .slider-dots--5{
        top: 180px;
      } 
      .slider-dots--6{
        left: -85px;
        top: 449px;
      } 
      .slider-dots--7{
        left: -85px;
        top: 312px;
      } 
      .slider-dots--8{
        top: 397px;
        &::before{
            left: auto;
            right: -0.5px;
        }
    }
    .shop__slider-inner {
       display: block;
    }
    .shop__slider-info{
        padding-top: 55px;
        padding-bottom: 50px;
    }
    .shop__slider{
        padding-left: 20px;
    }
    .surfboard-position--3 .surfboard-box__content {
        left: 60px;
    }
    .surfboard-position--3 .surfboard-box__content::before {
        width: 20px;
        right: auto;
        left: -32px;
   }
}

@media(max-width: 1240px){
    .holder-slider__info-title {
        font-size: 30px;
    }
}

@media(max-width: 1150px){
    .holder-slider__descr{
        padding: 160px 0 0 20px;
    }
    .surf, .holder{
        padding: 70px 0 0px;
    }
    
}


@media(max-width: 1102px){
    .slider-map{
        background-image: none;
        max-width: 100%;
        min-height: 280px;
    }
    .slider-dots__circle{
        display: none;
    }
    .slider-dots__content{
       display: block;
       &::before{
           display: none;
       }
    }
    .slider-dots{
        position: static;
    }
    .slider-map{
       .slick-list{
         overflow: hidden;
         min-height: 280px;
       }
       .slick-track{
        min-height: 280px;
        }
     }
     
     .slider-dots__content,
     .slider-dots--8 .slider-dots__content{
         transform: translateX(0);
     }
     .slider-dots__content{
         width: 285px;
         padding: 28px 31px 0 32px;
     }
     .slider-map .slick-current .slider-dots__content{
         background: rgba(#ccc, 0.7);
     }
   }

@media(max-width: 1040px){
    .header_arrows{
        display: none;
    }
    .menu{
        background-color: #000;
        z-index: 22;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        transform: translateX(-100%);
        justify-content: center;
        align-items: center;
        transition: all .5s;
        &.active{
            transform: translateX(0); 
        }
    }
    .menu-btn{
        display: flex;
    }
    .header_search{
        padding: 40px 15px 15px;
    }
    .slider-item_info{
        position: absolute;
        right: 20px;
        bottom: 17px;
        text-align: right;
    }
    .slider-item_info-text{
        font-size: 30px;
        line-height: 20px;
        margin-bottom: 15px;
    }
    .headline-left{
        font-size: 16px;
        line-height: 18px;
        padding-left: 65px;
    }
    .headline-right{
        font-size: 16px;
        line-height: 18px;
        padding-right: 65px;
    }
    .slider-item_info-suptext{
        margin-bottom: 5px;
    }

    .headline-left::before,
    .headline-right::before{
        width: 50px;
    }
    .slider-item_info-title{
        font-size: 55px;
        line-height: 55px;
        font-weight: 800;
        margin-bottom: 20px;
    }
    .slider-dotshead{
        right: 0;
        left: 10px;
        margin: 0 auto;
        bottom: 5px;
    }
    .dots-box{
        width: 130px;
    }
    .dots-box_namber{
        font-size: 18px;
    }
    .dots-box_name{
        line-height: 18px;
    }
    .surf__subtitle{
        padding-right: 20px;
    }
    .holder-slider__info{
        display: block;
    }
    .holder-slider__info-subtitle{
        position: static;
        transform: rotate(0deg);
        height: auto;
        width: auto;
        text-align: left;
    }
    .holder-slider__info-item{
        min-height: auto;
        margin-bottom: 20px;
        padding-left: 20px;
    }
    .holder-slider__info-title{
        padding-left: 0;
    }
    .holder-slider__descr{
        background-position: right -119px;
    }
    .holder{
        .title{
            span{
                padding-bottom: 0;
            }
        }
    }
    .title{
        span{
            font-size: 150px;
        }
    }
    .holder-slider__info-title .holder-pricing__span {
        left: 0;
       text-align: left;
      bottom: -10px;
    }
    .quantity-button.quantity-up {
        right: auto;
        left: 25px;
    }
    .quantity-button.quantity-down {
        right: auto;
    }
    .sleep .holder-slider__descr {
         background-position: right 10px; 
    }
}

@media(max-width: 960px){
    .slider-dotshead{
        display: none;
    }
}

@media(max-width: 767px){
    .surfboard-box__content{
        font-size: 20px;
        padding-left: 0;
        top: 35px;
        left: 0;
        width: 270px;
        &::before{
            display: none;
        }
    }
    .surfboard-position--1{
        .surfboard-box__content{
            left: -147px;
        }
    }
    .surfboard-position--3 .surfboard-box__content {
        top: -38px;
    }
    .shop__extras{
        padding-left: 75px;
    }
    .surfboard-position--1{
        left: 155px;
        top: 180px;
    }
}

@media(max-width: 720px){
    .title{
        span{
            font-size: 87px;
            padding-bottom: 80px;
            padding-left: 10px;
        }
    }
    .surf-slider .slider-arrows{
        top: 20px;
    }
    .surf-slider{
        padding-top: 65px;
    }
    .holder-slider__head{
        min-height: 720px;
    }
    .holder-slider__subtitle{
        padding: 100px 20px 0 0;
    }
}

@media(max-width: 680px){
    .shop__slider-content{
        display: block;
    }
    .shop__extras{
        padding-left: 0;
        padding-top: 35px;
    }
   .footer{
       text-align: center;
       padding: 100px 0 34px;
   }
}

@media(max-width: 640px){
    .holder-slider__descr{
        background-image: none !important;
    }
    .btn{
        font-size: 30px;
    }
}
  

@media(max-width: 520px){
    .header_aside{
        letter-spacing: 2.33333px;
    }
    .subtitle__head{
        font-size: 32px;
        padding-right: 0;
    }
    .descr__head{
        font-size: 32px;
    }
}

@media(max-width: 460px){
    .menu{
      padding-top: 16px;
    }
  .header_aside{
      display: block;
      text-align: center;
      padding-right: 0;
      height: 100vh ;
  }
  .header_search{
      padding: 15px 0 5px;
  }
  .header_date{
      padding: 0;
      font-size: 50px;
      line-height: 45px;
      span{
          font-size: 12px;
      }
  }
  .header_location{
      margin-top: 0;
  }
  .header_title{
      display: none;
  }
  .holder{
    background-position: right 85%;
      background-size: 140px;
  }
  .shop__slider-content{
      padding-left: 0;
  }
  .surfboard-position--2{
      left: 3px;
  }
}

@media(max-width: 425px){
    .slider-map .slider-dots__content,
    .slider-map .slick-current .slider-dots__content{
        display: block;
        width: 250px;
        padding: 28px 15px 0;
    }
    .holder-slider__info-title {
        font-size: 30px;
    }
    .holder-slider__info-subtitle {
    font-size: 14px;
    }
    .sleep .holder-slider__descr .descr__head .holder__star-list {
        position: static;
        margin-left: -15px;
    }
    .holder-slider__descr .descr__head.descr__head-sleep {
        padding-right: 0;
    }
    .quantity-nav {
        height: 22px;
    }
}

@media(max-width: 400px){
    .title h3 {
        font-size: 50px;
    }
    .title span {
        font-size: 55px;
        padding-left: 30px;
    }
    .holder-slider__descr .descr__head img {
        right: 30px;
        top: -10px;
    }
    .holder-slider__btn {
        margin-top: 35px;
    }
    .shop__extras-box{
        text-align: center;
        margin-right: 30px;
    }
    .title div {
        font-size: 90px;
        margin-bottom: 15px;
    }
}

